h3 {
  margin-block-end: .2em;
  margin-block-start: .2em;
}

.App {
  text-align: center;
}

.border {
  border: 1px solid black;
}

.border--left {
  border-left: 1px solid black;
}

.flex {
  display: flex;
}

.flex--1 {
  flex: 1;
}

.flex-align--center {
  align-items: center;
}

.flex--column {
  flex-direction: column;
}

.flex-justify--center {
  justify-content: center;
}

.flex--row {
  flex-flow: row;
}

.flex--space {
  justify-content: space-between;
}

.green {
  color: green;
}

.micro {
  font-size: 8px;
}

.min-width--400 {
  min-width: 400px;
}

.muted {
  color: grey;
}

.no-padding {
  padding: 0px !important;
}

.push {
  margin: 10px;
}

.push-double--right {
  margin-right: 20px;
}

.push--ends {
  margin-top: 10px;
  margin-bottom: 10px;
}

.push--right {
  margin-right: 10px;
}

.push--sides {
  margin-left: 10px;
  margin-right: 10px;
}

.red {
  color: red;
}

.soft--sides {
  padding-left: 10px;
  padding-right: 10px;
}

.square--50 {
  height: 50px;
  width: 50px;
}

.width--33 {
  width: 33%;
}

.width--50 {
  width: 50%;
}

.width--100 {
  width: 100%;
}
